  min-width: 50px;
  height: 50px;
  margin: 5px;
  font-family: monospace;
  font-size: 50px;
  line-height: 50px;
  position: relative;
}

.center {
  opacity: 0;
  perspective: 500px;
  justify-content: center;
  width: 100%;
  margin-top: 2%;
  display: flex;
  top: 50%;
  left: 0;
}

.top {
  z-index: 0;
  background-color: #000;
  border-radius: 10px 10px 0 0;
  width: 100%;
  height: 50%;
  position: relative;
  overflow: hidden;
}

.bottom {
  z-index: -1;
  transform-origin: center;
  background-color: #000;
  background-image: linear-gradient(#000, #000);
  border-radius: 10px;
  width: 100%;
  height: 100%;
  margin-top: -50%;
  position: relative;
}

.nextHalf {
  z-index: 2;
  transform-origin: bottom;
  background-color: #000;
  background-image: linear-gradient(#000, #000);
  border-radius: 10px 10px 0 0;
  width: 100%;
  height: 50%;
  margin-top: -100%;
  position: relative;
  overflow: hidden;
}

.nextFull {
  z-index: -3;
  background-color: #000;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  margin-top: -50%;
  position: relative;
}

.flip1 {
  animation: 1s ease-in flip1;
}

.flip2 {
  animation: 1s ease-out flip2;
}

@keyframes flip1 {
  0% {
    background-color: #000;
    transform: rotateX(0);
  }

  50% {
    background-color: #000;
    transform: rotateX(90deg);
  }

  100% {
    transform: rotateX(90deg);
  }
}

@keyframes flip2 {
  0% {
    transform: rotateX(-90deg);
  }

  50% {
    transform: rotateX(-90deg);
  }

  100% {
    background-color: #000;
    transform: rotateX(0);
  }
}

.hidden {
  opacity: 0;
  filter: blur(5px);
  transition: all 1s;
  transform: translateY(100%);
}

.show {
  opacity: 1;
  filter: blur();
  transform: translateY(0);
}

